import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message, Select, Spin, Checkbox } from 'antd';
import SelectAjax from 'components/common/input/SelectAjax';
import InputUserName from 'components/common/input/InputUserName';
import { getAllModule } from 'services/roleModule';
import to from 'await-to-js';
import { getUserById, updateUser, createUser } from 'services/managementUser';
// import routeConstant from 'routes/route/routeConstant';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    firstName: '',
    lastName: '',
    userName: '',
    email: '',
    password: '',
    userTypeId: '3',
    disabled: false,
    hasRole: false,
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'ForManagementUser' })
class ForManagementUser extends Component {
  state = {
    systemId: false,
    initialValue: {
      userTypeId: '',
      disabled: false,
    },
    loading: false,
    userId: '',
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.getUser(queryParamId);
    }
  };
  getUser = async queryParamId => {
    this.setState({
      userId: queryParamId,
      loading: true,
    });
    const { form } = this.props;
    const [error, response] = await to(getUserById(queryParamId));
    if (error) return;
    const initialValue = handleInitialValue(response.data);
    form.setFieldsValue(response.data);
    this.setState({ initialValue, loading: false });
  };

  handleSubmit = e => {
    const { userId } = this.state;
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        if (userId) {
          const [error] = await to(updateUser(userId, values));
          if (error) return;
          message.success('Chỉnh sửa thành công');
          this.props.history.push('/quan-ly-nguoi-dung');
        } else {
          const [error] = await to(createUser(values));
          if (error) return;
          message.success('Thêm thành công');
          this.props.history.push('/quan-ly-nguoi-dung');
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;

    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId } = this.state;
    const userTypeId = getFieldValue('userTypeId');
    return (
      <Card title={systemId ? 'Cập nhật tỉnh' : 'Thêm tỉnh'}>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item style={{ display: 'none' }} hasFeedback label="" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                })(<Input />)}
              </Form.Item>
              <Form.Item hasFeedback label="Họ" {...formItemLayout}>
                {getFieldDecorator('firstName', {
                  initialValue: initialValue.firstName,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item hasFeedback label="Tên" {...formItemLayout}>
                {getFieldDecorator('lastName', {
                  initialValue: initialValue.lastName,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item hasFeedback label="Tên đăng nhập" {...formItemLayout}>
                {getFieldDecorator('userName', {
                  initialValue: initialValue.userName,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<InputUserName disabled={this.state.userId} />)}
              </Form.Item>
              <Form.Item hasFeedback label="Email" {...formItemLayout}>
                {getFieldDecorator('email', {
                  initialValue: initialValue.email,
                  rules: [
                    {
                      type: 'email',
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Mật khẩu người dùng" hasFeedback>
                {getFieldDecorator('password', {
                  initialValue: initialValue.password,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Input.Password />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Phân quyền ">
                {getFieldDecorator('userTypeId', {
                  initialValue: initialValue.userTypeId,
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(
                  <Select disabled={initialValue.hasRole}>
                    <Option value={3}>User</Option>
                    <Option value={2}>Admin module</Option>
                  </Select>
                )}
              </Form.Item>
              {userTypeId === 2 && (
                <Form.Item {...formItemLayout} label="Module">
                  {getFieldDecorator('adminModuleId', {
                    initialValue: initialValue.adminModuleId,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(
                    <SelectAjax
                      keyLabel="name"
                      service={getAllModule}
                      // params={{ userTypeId: 3, disabled: false }}
                      // onChange={this.inputAddUser}
                    />
                  )}
                </Form.Item>
              )}

              <Form.Item {...formItemLayout} label="Khóa">
                {getFieldDecorator('disabled', {
                  initialValue: initialValue.disabled,
                  valuePropName: 'checked',
                  // rules: [
                  //   {
                  //     required: true,
                  //     message: 'Vui lòng nhập thông tin!',
                  //   },
                  // ],
                })(<Checkbox />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ForManagementUser {...props} />
  </Page>
));



